.checkboxes input {
  opacity: 0;
  font-size: 0;
  width: 0;
  height: 0;
  position: absolute;
  left: -9999px;
}
.checkboxes .icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #333;
  border-radius: 2px;
  cursor: pointer;
}
.checkboxes .label {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  font-size: 16px;
  padding-left: 5px;
}
.checkboxes input:checked + .icon {
  -webkit-box-shadow: inset 0 0 0 4px #fff;
  box-shadow: inset 0 0 0 4px #fff;
  background: #333;
}
.expansion-panel li:first-child .ep-header {
  border-radius: 2px 2px 0 0;
}
.expansion-panel li:last-child .ep-header {
  border-radius: 0 0 2px 2px;
}
.expansion-panel li:last-child .ep-header.is-open {
  border-radius: 0;
}
.expansion-panel li:last-child .ep-body {
  border-radius: 0 0 2px 2px;
}
.expansion-panel li .ep-header {
  -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  -o-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: #333;
  color: #fff;
  padding: 15px 30px;
  cursor: pointer;
  position: relative;
}
.expansion-panel li .ep-header:after {
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #fff transparent transparent transparent;
  position: absolute;
  right: 25px;
  top: 50%;
  margin-top: -2.5px;
}
.expansion-panel li .ep-header:hover {
  background: #3d3d3d;
}
.expansion-panel li .ep-header.is-open {
  background: #3d3d3d;
}
.expansion-panel li .ep-header.is-open:after {
  border-color: transparent transparent #fff transparent;
  border-width: 0 5px 5px 5px;
}
.expansion-panel li .ep-body {
  display: none;
  background: #999;
  padding: 15px 30px;
}
.expansion-panel li .ep-body p:last-child {
  margin-bottom: 0;
}
.modal {
  position: relative;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  background: #fff;
  border-radius: 2px;
}
.modal p:last-child {
  margin-bottom: 0;
}
.modal .modal-title {
  margin-bottom: 10px;
}
.radios {
  display: inline-block;
  vertical-align: middle;
  cursor: default;
}
.radios + .radios {
  margin-left: 15px;
}
.radios label {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.radios input {
  opacity: 0;
  font-size: 0;
  width: 0;
  height: 0;
  position: absolute;
  left: -9999px;
}
.radios .icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #333;
  border-radius: 50%;
  cursor: pointer;
}
.radios .label {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  font-size: 16px;
  padding-left: 5px;
}
.radios input:checked + .icon {
  -webkit-box-shadow: inset 0 0 0 4px #fff;
  box-shadow: inset 0 0 0 4px #fff;
  background: #333;
}
.select select {
  min-width: 200px;
}
@media screen and (max-width: 768px) {
  .responsive-table {
    overflow: auto;
  }
  .responsive-table table {
    width: 700px;
    min-width: 100%;
  }
}
table {
  width: 100%;
  text-align: center;
}
table th,
table td {
  padding: 15px;
  -webkit-box-shadow: 0 0 0 1px #d5d5d5;
  box-shadow: 0 0 0 1px #d5d5d5;
}
table th {
  background: #e0e0e0;
  cursor: default;
}
table th:first-child {
  border-radius: 2px 0 0 0;
}
table th:last-child {
  border-radius: 0 2px 0 0;
}
table td {
  -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  -o-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #f0f0f0;
}
table tr:last-child td:first-child {
  border-radius: 0 0 0 2px;
}
table tr:last-child td:last-child {
  border-radius: 0 0 2px 0;
}
table tr:hover td {
  background: #ebebeb;
}
/* Modules styles */
.m_breadcrumbs {
  position: relative;
}
.m_breadcrumbs ul {
  cursor: default;
  font-size: 0;
}
.m_breadcrumbs ul li {
  font-size: 16px;
  display: inline-block;
  vertical-align: middle;
}
.m_breadcrumbs ul li:after {
  content: '-';
  margin-left: 5px;
  margin-right: 5px;
}
.m_breadcrumbs ul li:last-child:after {
  display: none;
}
.m_breadcrumbs ul li a {
  color: #333;
}
.m_breadcrumbs ul li a:hover {
  text-decoration: underline;
}
.m_breadcrumbs ul li:last-child a {
  color: #ee6e73;
  cursor: default;
  text-decoration: none;
}
.m_footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 15px 0;
  text-align: center;
  background: #3b3a36;
}
.m_header {
  background: #3b3a36;
  height: 70px;
}
.m_pagination ul {
  cursor: default;
  font-size: 0;
}
.m_pagination li {
  display: inline-block;
  vertical-align: middle;
  margin-left: 4px;
  margin-right: 4px;
}
.m_pagination li a {
  min-width: 0;
  position: relative;
  margin-right: 0;
}
